<template>
  <wv-picker></wv-picker>
</template>

<script>

  export default {
    name: 'wv-datetime-picker',

    props: {
      confirmText: {
        type: String,
        default: ''
      },
      cancelText: {
        type: String,
        default: ''
      },
      slots: {
        type: Array
      },
      valueKey: String,
      value: Boolean
    },

    data () {
      return {
        currentValue: this.value
      }
    }
  }
</script>

<style scoped lang="scss">
</style>



