<template>
  <div>
    <wv-group>
      <wv-textarea placeholder="" :rows="1" :show-counter="false" v-model="content1"></wv-textarea>
    </wv-group>

    <wv-group>
      <wv-textarea placeholder="" :rows="3" v-model="content2" :max-length="10"></wv-textarea>
    </wv-group>

    <wv-group title="">
      <wv-textarea placeholder="" :rows="6" v-model="content1"></wv-textarea>
    </wv-group>
  </div>
</template>

<script>
export default {
  data () {
    return {
      content1: '',
      content2: null
    }
  }
}
</script>



